import { useStaticQuery, graphql } from 'gatsby';
import React from 'react';

import CodeItem from '../components/CodeItem';
import Layout from '../components/Layout';

const item1 = {
  demoLink: 'Example Code Item Title',
  description:
    'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Maecenas hendrerit sollicitudin ipsum. Integer dolor nisl, varius in est in, egestas maximus leo. Vivamus semper tincidunt augue, id blandit metus ultricies a.',
  repositoryLink: 'Example Code Item Title',
  tags: ['Tag 1', 'Tag 2'],
  title: 'Example Code Item Title',
};

export default () => {
  const data = useStaticQuery(graphql`
    query {
      allMarkdownRemark(
        filter: { frontmatter: { type: { eq: "code" } } }
        sort: { order: DESC, fields: [frontmatter___date] }
      ) {
        edges {
          node {
            html
            frontmatter {
              date
              demoLink
              path
              repositoryLink
              tags
              title
              type
            }
          }
        }
      }
    }
  `);

  const {
    allMarkdownRemark: { edges },
  } = data;

  return (
    <Layout appHeaderColor={'--text-color-alt1'} bgColor={'--color-alt1'}>
      {edges.map(edge => {
        const nodeData = {
          description: edge.node.html,
          ...edge.node.frontmatter,
        };
        return <CodeItem data={nodeData} key={nodeData.path} />;
      })}
    </Layout>
  );
};
